import { css, CSSResultArray, cssSelector, cssVariableAsVar, GenerateStyles, style } from '@umetrics/sartorius-ui-base';

export const stepStyle: GenerateStyles = (tagName) => {
    const { root, is } = cssSelector(tagName);

    return [
        style,
        css`
            @keyframes expand {
                from {
                    opacity: 0;
                    display: none;
                }
                1% {
                    display: flex;
                }
                to {
                    opacity: 1;
                }
            }
            :host,
            :host::before,
            :host::after,
            :host *,
            :host *::before,
            :host *::after {
                box-sizing: border-box;
            }

            ${root} {
                overflow: hidden;
                display: none;
            }

            ${is('container')} {
                height: 100%;
                border: 1px solid ${cssVariableAsVar('color_secondary_3')};
                margin-top: 16px;
                padding: 16px;
                width: 100%;
            }

            ${is('open')} {
                animation: expand 0.5s;
                display: flex;
                height: 100%;
            }
        `,
    ] as CSSResultArray;
};
