import {
    BaseElement,
    booleanConverter,
    createClassName,
    defineElement,
    html,
    property,
    query,
    state,
} from '@umetrics/sartorius-ui-base';
import { version } from './metadata';
import { stepperStyle } from './stepperStyle';
import { StepperProperties } from './stepperProperties';
import { StepEvents } from './stepEvents';
import { Step } from './step';
import { StepperEvents } from './stepperEvents';
import { ActionCallbackFunc } from './stepProperties';
import { EventDispatcherMixin, ResizableBehaviorMixin, ResizeAwareMixin } from '@umetrics/sartorius-ui-mixins';

@defineElement({
    tagName: 'stepper',
    styles: stepperStyle,
    version,
    //    sub: [Button]
})
export class Stepper
    extends EventDispatcherMixin(ResizeAwareMixin(ResizableBehaviorMixin(BaseElement)))
    implements StepperProperties
{
    // Static members
    public static readonly BUTTON_SLOT = 'button-slot';
    public static readonly HEADING_SLOT = 'heading-slot';

    private static Button;

    private static animations = {
        enter: {
            fromLeft: [
                {
                    transform: 'scaleX(0)',
                    transformOrigin: 'center left',
                },
                {
                    transform: 'scaleX(1)',
                    transformOrigin: 'center left',
                },
            ],
            fromRight: [
                {
                    transform: 'scaleX(0)',
                    transformOrigin: 'center right',
                },
                {
                    transform: 'scaleX(1)',
                    transformOrigin: 'center right',
                },
            ],
        },
    };

    // Public members

    @property({ type: String, reflect: true })
    heading = '';
    @property({ type: Number, reflect: true })
    get value() {
        return this._value;
    }
    set value(val) {
        if (this._value !== val && val > -1 && val < this._tab.length) {
            this._openNewTab(this._tab[val]);
        }
    }
    @property({ type: Boolean, converter: booleanConverter, reflect: true, attribute: 'full-height' })
    fullHeight = false;

    // Private members
    @query('div[class*="-steps"]')
    private _stepperDiv;
    @query('div[class*="-panel"]')
    private _panelDiv;
    @query('div[class*="-p-wrapper"]')
    private _panelWrapper;
    private _value = -1;
    @state()
    private readonly _tab: Step[] = [];
    private _startX;
    private _scrollLeft;
    private _prevIndex = 0;
    private _shouldCenter = false;
    private _focusedElement;

    // Public methods
    constructor() {
        super();

        this._navigationClick = this._navigationClick.bind(this);
        this._mouseDownHandler = this._mouseDownHandler.bind(this);
        this._mouseMoveHandler = this._mouseMoveHandler.bind(this);
        this._mouseLeaveHandler = this._mouseLeaveHandler.bind(this);
        this._tabRequestAddHandler = this._tabRequestAddHandler.bind(this);
        this._tabRequestOpenHandler = this._tabRequestOpenHandler.bind(this);
        this._tabRequestStateHandler = this._tabRequestStateHandler.bind(this);
        this.addEventListener(StepEvents.Add, this._tabRequestAddHandler);
        this.addEventListener(StepEvents.RequestOpen, this._tabRequestOpenHandler);
        this.addEventListener(StepEvents.RequestStateChange, this._tabRequestStateHandler);
        this.addEventListener('focusout', () => (this._focusedElement = null));
        this.addEventListener('keydown', this._handleKeydown);
    }

    connectedCallback() {
        this.style.setProperty('display', 'none');
        const imports: any[] = [];
        imports.push({
            module: () => import('@umetrics/sartorius-ui-button'),
            elem: ['Button'],
        });
        Stepper.dynSubRegister(imports, () => {
            super.connectedCallback();
            this.style.removeProperty('display');
        });
    }

    disconnectedCallback() {
        if (this._panelWrapper) {
            this._panelWrapper.removeEventListener('mousedown', this._mouseDownHandler);
            this._panelWrapper.removeEventListener('mouseup', this._mouseLeaveHandler);
            this._panelWrapper.removeEventListener('mouseleave', this._mouseLeaveHandler);
        }
        this.removeEventListener(StepEvents.Add, this._tabRequestAddHandler);
        this.removeEventListener(StepEvents.RequestOpen, this._tabRequestOpenHandler);
        this.removeEventListener(StepEvents.RequestStateChange, this._tabRequestStateHandler);
        this.removeEventListener('focusout', () => (this._focusedElement = null));
        this.removeEventListener('keydown', this._handleKeydown);
        this._uninstallResizeObserver(this._panelDiv);
        super.disconnectedCallback();
    }

    protected firstUpdated() {
        this._installResizeObserver(this._panelDiv);
    }

    // Before render
    protected update(changedProperties) {
        super.update(changedProperties);
    }

    protected render() {
        const [baseClassName, rootClasses] = createClassName(this.tagName.toLowerCase(), {
            ['full-height']: this.fullHeight,
        });

        const viewHtml = this._tab.map((t, index) => {
            return this._addView(baseClassName, t, index);
        });

        let buttonText = '';
        let buttonState = false;
        let actionButton = false;
        if (this._tab && this._tab.length > 0) {
            const index = this._value > -1 ? this._value : 0;
            buttonText = this._tab[index].buttonText;
            buttonState = !this._tab[index].completed;
            actionButton = this._tab[index].actionButton;
        }

        const clickHandler = actionButton ? this._actionButtonClickHandler : this._nextButtonClickHandler;
        const buttonHtml = buttonText
            ? html`<${Stepper.Button.tag} class="${baseClassName}-action-button" @click=${clickHandler} disabled="${buttonState}">${buttonText}</${Stepper.Button.tag}>`
            : html``;

        const headingHtml = this.heading ? html`${this.heading}` : html`<slot name="${Stepper.HEADING_SLOT}"></slot>`;

        return html`
            <div class="${rootClasses}">
                <div class="${baseClassName}-p-wrapper">
                    <div class="${baseClassName}-left">
                        <div class="${baseClassName}-heading">${headingHtml}</div>
                    </div>
                    <div class="${baseClassName}-panel">
                        <div class="${baseClassName}-steps">${viewHtml}</div>
                    </div>
                    <div class="${baseClassName}-right">${buttonHtml}</div>
                </div>
                <div class="${baseClassName}-content">
                    <slot></slot>
                </div>
            </div>
        `;
    }

    protected override _resizeObserverElem() {
        return this._panelDiv;
    }

    protected override _resizeObserverCallback(entries: ResizeObserverEntry[]) {
        entries.forEach((entry) => {
            if (entry.contentRect.width < this._stepperDiv.scrollWidth) {
                this._panelWrapper.addEventListener('mousedown', this._mouseDownHandler);
                this._panelWrapper.addEventListener('mouseup', this._mouseLeaveHandler);
                this._panelWrapper.addEventListener('mouseleave', this._mouseLeaveHandler);
                this._panelDiv.style.cursor = 'grab';
                this._shouldCenter = true;
            } else {
                this._panelWrapper.removeEventListener('mousedown', this._mouseDownHandler);
                this._panelWrapper.removeEventListener('mouseup', this._mouseLeaveHandler);
                this._panelWrapper.removeEventListener('mouseleave', this._mouseLeaveHandler);
                this._panelDiv.style.cursor = 'unset';
                this._shouldCenter = false;
            }
        });
    }

    private _actionButtonClickHandler(e) {
        e.stopPropagation();
        this._dispatchActionEvent();
        if (this._tab[this._value].actionCallback !== undefined) {
            (this._tab[this._value].actionCallback as unknown as ActionCallbackFunc)(e);
        }
    }

    private _nextButtonClickHandler(e) {
        e.stopPropagation();
        this._navigationClick(this._value + 1);
    }

    private _enterLeft(tab: Step) {
        const border = tab.querySelector('div[class$="-button-border"]')!;
        const animationKeyframes = Stepper.animations['enter'].fromLeft;
        const animationOptions = {
            duration: 200,
            iterations: 1,
        };
        const fadeInAnimation = border.animate ? border.animate(animationKeyframes, animationOptions) : undefined;
        if (fadeInAnimation) {
            fadeInAnimation.onfinish = () => {};
        }
    }

    private _enterRight(tab: Step) {
        const border = tab.querySelector('div[class$="-button-border"]')!;
        const animationKeyframes = Stepper.animations['enter'].fromRight;
        const animationOptions = {
            duration: 200,
            iterations: 1,
        };
        const fadeInAnimation = border.animate ? border.animate(animationKeyframes, animationOptions) : undefined;
        if (fadeInAnimation) {
            fadeInAnimation.onfinish = () => {};
        }
    }

    private _addView(baseClassName, tab, index) {
        const text = tab.heading ? tab.heading : 'Tab ' + index;
        const disabled = tab.disabled;

        return html` <button
            title="${text}"
            tabindex=${tab.disabled || index !== 0 ? -1 : 0}
            custom-disabled="${disabled}"
            ?disabled="${disabled}"
            class="${baseClassName}-button ${baseClassName}-button-${tab.open
                ? 'open'
                : 'closed'} ${baseClassName}-button-${tab.completed && !tab.open ? 'completed' : ''}"
            style="${disabled ? 'pointer-events: none; color: #878787;' : ''}"
            @click="${tab.open ? null : () => this._navigationClick(index)}"
        >
            <div class="${baseClassName}-button-text">${text}</div>
            <div class="${baseClassName}-button-border"></div>
        </button>`;
    }

    private _handleKeydown(e) {
        if (e.target === this) {
            const arr = this.shadowRoot?.querySelectorAll('button');
            const elements: any = [].slice.call(arr);
            const removeItems: any = [];
            elements.forEach((elem) => {
                if (elem.getAttribute('custom-disabled') === 'true') {
                    removeItems.push(elem);
                }
            });
            removeItems.forEach((el) => {
                const removeIndex = elements.indexOf(el);
                elements.splice(removeIndex, 1);
            });
            if (e.key === 'ArrowRight') {
                this._handleArrowRight(e, elements);
            } else if (e.key === 'ArrowLeft') {
                this._handleArrowLeft(e, elements);
            }
        }
    }

    private _handleArrowRight(e, elements) {
        e.preventDefault();
        let currentIndex = this._focusedElement ? elements.indexOf(this._focusedElement) : 0;
        if (elements.length <= currentIndex + 1) {
            currentIndex = -1;
        }
        elements[currentIndex + 1].focus();
        elements[currentIndex + 1].tabindex = 0;
        this._focusedElement = elements[currentIndex + 1];
        elements[currentIndex >= 0 ? currentIndex : 0].tabindex = -1;
    }

    private _handleArrowLeft(e, elements) {
        e.preventDefault();
        let currentIndex = this._focusedElement ? elements.indexOf(this._focusedElement) : 0;
        if (currentIndex - 1 < 0) {
            currentIndex = elements.length;
        }
        elements[currentIndex - 1].focus();
        elements[currentIndex - 1].tabindex = 0;
        this._focusedElement = elements[currentIndex - 1];
        elements[currentIndex <= elements.length - 1 ? currentIndex : elements.length - 1].tabindex = -1;
    }

    private _navigationClick(index) {
        const arr = this.shadowRoot?.querySelectorAll('button');
        const elements: any = [].slice.call(arr);
        const selectedButton = elements[index];

        if (this._shouldCenter) {
            let hiddenLeft = 0;
            for (let i = 0; i < index; i++) {
                hiddenLeft += elements[i].getBoundingClientRect().width;
            }
            if (index > 0) {
                hiddenLeft -= 40;
            }
            requestAnimationFrame(() => {
                this._stepperDiv.scroll({
                    left: hiddenLeft,
                    top: 0,
                    behavior: 'smooth',
                });
            });
        }
        if (index >= this._prevIndex) {
            this._enterLeft(selectedButton);
        } else {
            this._enterRight(selectedButton);
        }
        this._tab[this._value].fold(() => {
            this._prevIndex = index;
            this._tab[index].expand(() => {
                this._setValue(index);
                this.notifyResize();
            });
        });

        this.requestUpdate();
    }

    private _mouseDownHandler(e) {
        this._stepperDiv.style.userSelect = 'none';
        this._panelDiv.style.cursor = 'grabbing';
        this._panelWrapper.addEventListener('mousemove', this._mouseMoveHandler);
        this._startX = e.pageX - this._stepperDiv.offsetLeft;
        this._scrollLeft = this._stepperDiv.scrollLeft;
    }

    private _mouseMoveHandler(e) {
        const x = e.pageX - this._stepperDiv.offsetLeft;
        const walk = x - this._startX;
        this._stepperDiv.scrollTo({ left: this._scrollLeft - walk });
    }

    private _mouseLeaveHandler() {
        this._panelDiv.style.cursor = 'grab';
        this._stepperDiv.style.userSelect = 'unset';
        this._panelWrapper.removeEventListener('mousemove', this._mouseMoveHandler);
    }

    private _tabRequestAddHandler(e: Event) {
        e.stopPropagation();
        const evt = e as CustomEvent;
        const element = evt.detail.element as Step;

        if (this._tab.indexOf(element) > -1) {
            return;
        }

        if (element.open) {
            this._tab.forEach((tab) => {
                if (tab.open) {
                    tab.fold(() => element.expand(null));
                }
            });
        } else if (this._tab.length === 0) {
            element.expand(null);
        }

        if (element.index > -1) {
            // May collide if element index is not unique. Client fault!
            this._tab.splice(element.index, 0, element);
        } else {
            // No index set by client. Pick next "free"
            this._tab.push(element);
            element.index = this._tab.length === 1 ? 0 : this._tab[this._tab.length - 2].index + 1;
        }

        this._tab.sort((x, y) => (x.index < y.index ? -1 : 1));

        if (element.open) {
            this._setValue(element.index);
        }

        element.addEventListener(StepEvents.Remove, (e2) => this._tabRequestRemoveHandler(e2, element), { once: true });
        this.requestUpdate();
    }

    private _tabRequestRemoveHandler(e: Event, element) {
        e.stopPropagation();
        const index = this._tab.indexOf(element);
        if (index > -1) {
            this._tab.splice(index, 1);
        }
        if (element.open && this._tab.length > 0 && this._tab[0]) {
            this._tab[0].expand(null);
            this._setValue(0);
        }
        this.requestUpdate();
    }
    private _setValue(val) {
        this.setAttribute('value', '' + val);
        this._value = val;
    }

    private _tabRequestStateHandler() {
        //e:Event) {
        //const evt = e as CustomEvent;
        //const step = evt.detail.element;
        this.requestUpdate();
    }

    private _tabRequestOpenHandler(e: Event) {
        const evt = e as CustomEvent;
        this._openNewTab(evt.detail.element);
        e.preventDefault();
        return false;
    }

    private _openNewTab(nextOpen) {
        const prevValue = this._value;
        const prevOpen = prevValue > -1 ? this._tab[prevValue] : null;
        const nextValue = this._tab.indexOf(nextOpen);
        if (prevOpen) {
            // return false or preventDefault in eventhandler to block tab-change
            if (this._dispatchChangeEvent(prevValue, nextValue)) {
                prevOpen.fold(() => {
                    nextOpen.expand();
                    this._setValue(nextValue);
                });
            }
        } else {
            // First open
            nextOpen.expand();
            this._setValue(nextValue);
        }

        this.requestUpdate();
    }

    private _dispatchActionEvent() {
        return this.dispatch(StepperEvents.Action, { value: this._value });
    }

    private _dispatchChangeEvent(prevValue: number, nextValue: number) {
        return this.dispatch(StepperEvents.Change, { value: nextValue, prev: prevValue });
    }
}
