import {
    BaseElement,
    booleanConverter,
    createClassName,
    defineElement,
    html,
    property,
    query,
    state,
} from '@umetrics/sartorius-ui-base';
import { version } from './metadata';
import { ActionCallbackFunc, StepProperties, StepResolverFunc } from './stepProperties';
import { stepStyle } from './stepStyle';
import { StepEvents } from './stepEvents';
import { EventDispatcherMixin } from '@umetrics/sartorius-ui-mixins';

@defineElement({ tagName: 'step', styles: stepStyle, version })
export class Step extends EventDispatcherMixin(BaseElement) implements StepProperties {
    // Static members

    // Public members
    @property({ type: String })
    get heading() {
        return this._heading;
    }
    set heading(val) {
        if (this._heading !== val) {
            const prev = this._heading;
            this._heading = val;
            if (!this._dispatchEvent(StepEvents.RequestStateChange, { element: this })) {
                this._heading = prev;
            }
        }
    }
    @property({ type: Number, reflect: true })
    index = -1;
    @property({ type: Boolean, converter: booleanConverter })
    container = false;
    @property({ type: String, attribute: 'button-text' })
    get buttonText() {
        return this._buttonText;
    }
    set buttonText(val) {
        if (this._buttonText !== val) {
            const prev = this._buttonText;
            this._buttonText = val;
            if (!this._dispatchEvent(StepEvents.RequestRecalculate, { element: this })) {
                this._buttonText = prev;
            }
        }
    }

    @property({ type: Boolean, converter: booleanConverter })
    get completed() {
        return this._completed;
    }
    set completed(val) {
        if (this._completed !== val) {
            this._completed = val;
            if (!this._dispatchEvent(StepEvents.RequestStateChange, { element: this })) {
                this._completed = !val;
            }
        }
    }

    @property({ type: Boolean, converter: booleanConverter })
    get disabled() {
        return this._disabled;
    }
    set disabled(val) {
        if (this._disabled !== val) {
            this._disabled = val;
            if (!this._dispatchEvent(StepEvents.RequestStateChange, { element: this })) {
                this._disabled = !val;
            }
        }
    }

    @property({ type: Boolean, converter: booleanConverter })
    get open() {
        return this._open;
    }
    set open(val) {
        if (this._open !== val) {
            if (this._dispatchEvent(val ? StepEvents.RequestOpen : StepEvents.RequestClose, { element: this })) {
                if (val) {
                    this.expand(null);
                } else {
                    this.fold(null);
                }
            }
        }
    }
    @property({ type: Boolean, attribute: 'action-button', converter: booleanConverter })
    get actionButton() {
        return this._actionButton;
    }

    set actionButton(val) {
        if (this._actionButton !== val) {
            this._actionButton = val;
            if (!this._dispatchEvent(StepEvents.RequestStateChange, { element: this })) {
                this._actionButton = !val;
            }
        }
    }
    @property({ type: Object, attribute: false })
    actionCallback?: ActionCallbackFunc = undefined;
    @property({ type: Function })
    stepResolver?: StepResolverFunc = undefined;

    private _buttonText = '';
    private _open = false;
    private _disabled = false;
    private _completed = false;
    private _actionButton = false;
    private _heading;

    @query('slot')
    private readonly _slot!: HTMLSlotElement;

    @state()
    private _withContent = false;

    // Public methods

    connectedCallback() {
        super.connectedCallback();
        this._slotChanged = this._slotChanged.bind(this);

        this._dispatchEvent(StepEvents.Add, { element: this });
    }

    disconnectedCallback() {
        this._dispatchEvent(StepEvents.Remove);
        super.disconnectedCallback();
    }

    public fold(callback: ((e) => void) | null) {
        this._open = false;
        this.removeAttribute('open');
        this.requestUpdate();
        if (callback) {
            callback(null);
        }
    }

    public expand(callback: ((e) => void) | null) {
        if (!this._withContent) {
            if (this.stepResolver !== undefined) {
                this.stepResolver(this).then((r) => {
                    this._tabResolutionCallback(r, callback);
                });
                return;
            }
            // Else no content... client need to handle change event and provide slot
        }
        this._open = true;
        this.setAttribute('open', 'true');
        this.requestUpdate();
        if (callback) {
            callback(null);
        }
    }

    // Protected methods

    protected async firstUpdated(changedProperties) {
        super.firstUpdated(changedProperties);
        /**
         * Due to safari bug we need to manually setup each slot
         */
        this._setupSlot(this._slot);
    }

    protected render() {
        const [, rootClasses] = createClassName(this.tagName.toLowerCase(), {
            open: this.open,
            container: this.container,
        });
        return html` <div class="${rootClasses}"><slot @slotchange="${this._slotChanged}"></slot></div> `;
    }

    private _dispatchEvent(name: string, detail: any = null) {
        return this.dispatch(name, detail);
    }

    private _slotChanged(e: Event) {
        const target = e.target as HTMLSlotElement;
        this._setupSlot(target);
    }

    private _setupSlot(target: HTMLSlotElement) {
        const nodes = target && target.assignedNodes().filter((node) => node.nodeType === 1);
        if (nodes && nodes.length > 0) {
            this._withContent = true;
        }
    }

    private _tabResolutionCallback(r, callback) {
        if (typeof r === 'string') {
            this.insertAdjacentHTML('beforeend', r);
        } else if (r instanceof HTMLElement) {
            this.appendChild(r);
        }
        this._withContent = true;
        this.expand(callback);
    }
}
