import { css, CSSResultArray, cssSelector, cssVariableAsVar, GenerateStyles, style } from '@umetrics/sartorius-ui-base';

const cssNoMin = css; // html-minifier-terser fail!

export const stepperStyle: GenerateStyles = (tagName) => {
    const { root, child, is } = cssSelector(tagName);

    return [
        style,
        cssNoMin`
            :host,
            :host::before,
            :host::after,
            :host *,
            :host *::before,
            :host *::after {
                box-sizing: border-box;
            }

            :host {
                display: block;
                width: 100%;
            }
            :host([full-height]) {
                height: 100%;
            }
            ${is('full-height')} {
                display: flex;
                flex-direction: column;
                height: 100%;
            }
            ${is('full-height')} ${child('content')} {
                flex-grow: 1;
                height: calc(100% - 40px);
            }
            ${root} {
                width: 100%;
            }
            ${child('p-wrapper')} {
                display: flex;
                flex-direction: row;
                width: 100%;
            }

            ${child('panel')} {
                width: 100%;
                margin: 0px;
                padding: 0px;
                display: flex;
                flex-direction: row;
                align-items: flex-end;
                overflow: auto;
                justify-content: flex-end;
            }

            ${child('left')} {
                display: flex;
                margin-right: 5px;
                flex-direction: row;
                justify-content: start;
                align-items: center;
                white-space: nowrap;
            }
            ${child('right')} {
                display: flex;
                flex-grow: 2;
                flex-direction: row;
                justify-content: end;
                align-items: center;
                height: 100%;
            }

            ${child('steps')} {
                display: flex;
                flex-direction: row;
                max-width: 100%;
                height: 40px;
                gap: 1px;
                overflow: hidden;
            }

            ${child('action-button')} {
                padding-left: 4px;
            }

            ${child('button')} {
                display: flex;
                flex-direction: column;
                justify-content: end;
                position: relative;
                background: 0;
                border: 0;
                outline: 0;
                font-size: 14px;
                user-select: none;
                height: 100%;
                padding: 0px;
            }
            ${child('button')}:focus-visible:before {
                content: ' ';
                display: block;
                position: absolute;
                inset: 0px;
                border: 2px solid black;
            }

            ${child('button-text')} {
                margin-left: 20px;
                margin-right: 20px;
                padding: 0 16px;
                white-space: nowrap;
            }

            ${child('button-border')} {
                width: 100%;
                height: 5px;
                background-color: ${cssVariableAsVar('color_secondary_3')};
                margin-bottom: 0;
                margin-top: 4px;
            }

            ${child('button-open')} ${child('button-border')} {
                background-color: ${cssVariableAsVar('color_primary_1')};
            }

            ${child('button-completed')} ${child('button-border')} {
                background-color: ${cssVariableAsVar('color_primary_3')};
            }

            ${child('button-open')} {
                pointer-events: none;
                font-weight: 600;
            }

            ${child('button-closed')}:not([disabled]):hover ${child('button-border')} {
                background-color: ${cssVariableAsVar('color_primary_3')};
            }

            ${child('button-closed')}:not([disabled]):hover {
                cursor: pointer;
            }
        `,
    ] as CSSResultArray;
};
